<template>
  <div id="encyclopedias">
    <new-layout>
      <template slot="top">
        <announce-header>评估</announce-header>
      </template>
      <template slot="center">
        <ul>
          <li class="identity mt15">
            <span>身份证</span>
            <input type="text" placeholder="请输入身份证" v-model="targetId"/>
          </li>
          <div class="identity_btn" @click="search(targetId)">查询</div>
       </ul>


        <van-collapse v-model="activeNames" accordion v-for="(item,index) in result1" v-if="result1Show" :key="index">
          <van-collapse-item :name="index">
           <div slot="title">{{item.name}}  <span  :class="item.riskLevelName == '低风险'?'lowrisk':(item.riskLevelName == '中等风险'?'mediumrisk':'highrisk')">{{item.riskLevelName}}</span></div>
            <div><b>分析建议：</b>
               <div class="indent" v-for="(items,index) in result2"  :key="index">

                  <span  v-html="itemss" v-for="(itemss,index) in items" :key="index"></span>
               </div>
            </div>
          </van-collapse-item>
     </van-collapse>





        <van-collapse v-model="activeNames" accordion v-for="(item,index) in result" v-if="resultShow"  :key="index">
          <van-collapse-item :name="index">
            <div slot="title">{{item.name}}  <span  :class="item.riskLevelName == '低风险'?'lowrisk':(item.riskLevelName == '中等风险'?'mediumrisk':'highrisk')">{{item.riskLevelName}}</span></div>
            <p><b>分析建议：</b><div v-html="item.suggest.replace(/\n|\r\n/g,'<br/>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;')" class="indent"></div></p>
          </van-collapse-item>
      </van-collapse>



      </template>
     <template slot="bottom">
     </template>
    </new-layout>
  </div>
</template>

<script>
 export default {
    data(){
      return {
        targetId: '', // 3205831988060000
        activeNames: ['1'],
        message: '健。',
        result: [],
        result1: [],
        result2: [],
        result1Show: false,
        resultShow: false
      }
    },
    methods:{
      parseEvaluateContent(data) {

        var content = data.suggest;


        if (data.code == 'hypertension' || data.code == 'osteoporosis'|| data.code == 'diabetes'|| data.code == 'af'|| data.code == 'icvd'|| data.code == 'cvd'|| data.code == 'chd') {
        // alert('报错了111111')
          content = content.replace(/(2\. *运动：|3\. *吸烟：|4\. *饮酒：|5\. *心理：)/g, "@#$1");
        }




        var arr = content.split('@#');

         var ps = [];


        for (var i = 0; i < arr.length; i++) {
          var a = arr[i];
          a = $.trim(a);
          if (a && a.length > 0 && a !== '3.吸烟：') {
            ps.push(a);
          }
        }


        var s = [];
        for (var i = 0; i < ps.length; i++) {
          s.push( "\t" + ps[i] + "\n")
        }

         console.log(s)
        return s;
      },
      search(targetId){
        if(!targetId){
          this.$toast('身份证不能为空')
          return false
        }

        this.$router.push(`/nearsearchDetail?targetId=${targetId}`)

      }
    },
   filters: {


   },
   created(){
     }
  }
</script>

<style scoped lang="scss">
.identity{
  width: 96%;
  margin: auto;
  height: 80px;
  line-height: 80px;
  font-size: 24px;
}
.identity input{
  width: 70%;
  border: 1px solid #dad8d8;
  padding-left: 15px;
}
  .identity_btn{
    width: 80%;
    height: 90px;
    line-height: 90px;
    background: #6eafff;
    text-align: center;
    color: #ffffff;
    margin:40px auto;
    border-radius: 10px;
  }
  .mt15{
    margin-top: 15px;
  }
  .lowrisk{
      color: #3ece76;
  }
  .mediumrisk{
    color: #ed9e1b;
  }
  .highrisk{
    color: #ff4444;
  }
  .van-cell__title span{
    font-weight: bold;
  }
  b{
    color: #000000;
    font-weight: bold;
  }
  .indent{
    text-indent: 42px;
  }
</style>



